

@import url('https://fonts.googleapis.com/css2?family=Inter&display=swap');


*{
padding: 0;
margin: 0;
font-family: "Inter", sans-serif;
font-optical-sizing: auto;
background-color:#f5f5f5;
}
body{
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
/* margin: 0 auto; */
align-items: center;
background-color: #2c3a47;
}
.container{
    width: 500px;
    height: 100vh;
    overflow: hidden;
    position: relative;
    border-radius: 20px;
    padding: 10px 20px;
    text-align: center;
}
p{
    /* position: absolute; */
    display:block;
    margin: 10px 20px;

}
h3{
    text-align: start;
    margin: 1rem 0.6rem;
}
.amount{
    width: auto;
}
input{
    width: 90%;
    border-color: #ccc;
    border-radius: 10px;
    padding: 12px 15px;
}
.displaycurrency{
    margin: 20px 20px;
    border: 1px dashed #3499db;
    border-radius: 4px;
}
h2{
    margin: 10px 12px;
    padding: 13px 12px;
    color:#3499db ;
    border-top: 1px dashed #3499db;
    border-bottom: 1px dashed #3499db;

}
img{
    width: 110px;
    height: 110px;;
}
@media only screen and (max-width: 600px) {
  img{
    width: 5rem;
    height: 5rem;
  }
  .container{
    overflow: auto;
  
  }
  input{
    width: 60%;
  }
  h3{
    margin-left: 5rem;
  }
  }
  
  @media only screen and (min-width: 610px) and (max-width:1194px){
    .container{
        overflow:hidden;
    height: auto;  
    }

  }
